<template>
    <div class="page">
        <div class="searchWrap">
            <div class="search">
                <table>
                    <tr>
                        <td>
                            &nbsp;&nbsp;用户名:
                            <template>
                                <Input v-model="searchInfo.userName" clearable placeholder="请输入用户名" style="width:100px;"/>
                            </template>
                            <template>
                                <div class="searchBtnList">
                                    <Button type="primary" icon="ios-add-circle" @click="showNew">新增</Button>
                                    <Button icon="ios-refresh-circle" @click="reflesh">重置</Button>
                                </div>
                            </template>
                        </td>
                    </tr>
                </table>
            </div>
        </div>

        <div class="tableList">
            <template>
                <Table :height="height-100" border stripe :loading="isLoading" :columns="columnsRealTime" :data="realTimeDataList">
                    <template slot-scope="{ row, index }" slot="action">
                        <Button type="primary" size="small" @click="showEdit(row)">修改</Button>&nbsp;&nbsp;
                        <!-- <Button type="error" size="small" @click="deleteAction(row)">删除</Button> -->
                    </template>
                    <template slot-scope="{ row, index }" slot="status">
                        <template>
                            <i-switch size="large" :value="row.status" :true-value="1" :false-value="0" @on-change="enableOrNot(row.status==0?'1':'0', row)">
                                <span slot="open">启用</span>
                                <span slot="close">禁用</span>
                            </i-switch>
                        </template>
                    </template>
                </Table>
            </template>
        </div>
        <div class="pageWrap">
            <template>
                <Page :total="count" @on-change="pageSizeChange" @on-page-size-change="pageRowChange" :page-size="50" :page-size-opts="[50, 100, 150]" show-sizer showTotal />
            </template>
        </div>

        <template>
            <Modal v-model="isDetail" class="noContentPadding noHeaderModal" :mask-closable="false" width="30" :styles="{top: '5%'}" title="新增/修改账号" @on-cancel="isDetail=false">
                <div class="modalTable">
                    <div class="detail" :style="{maxHeight: (height-200)+'px',overflowY: 'auto'}">
                        <table>
                            <tr>
                                <td>
                                    <span class="request">*</span>用户名：
                                    <div><Input v-model="itemInfo.username" placeholder="填写用户名,可由英文字母、数字组成" style="width: 100%" /></div>
                                    <div class="tips">必填，可由英文字母、数字组成</div>
                                </td>
                            </tr>
                            <tr>
                                <td >
                                    昵称：
                                    <div><Input v-model="itemInfo.nickname" placeholder="填写昵称,可以是中文" style="width: 100%" /></div>
                                    <div class="tips">可以是中文</div>
                                </td>
                            </tr>
                            
                            <tr>
                                <td>
                                    <span class="request">*</span>密码：
                                    <div><Input v-model="itemInfo.password" placeholder="填写登录密码,至少6个字符,最多18个字符" style="width: 100%" /></div>
                                    <div class="tips">必填，至少6个字符,最多18个字符</div>
                                </td>
                            </tr>
                            
                            <tr>
                                <td>
                                    <span class="request">*</span>角色：
                                    <div>
                                        <template>
                                            <Select v-model="itemInfo.roleId" clearable placeholder="选择角色" style="width: 100%;">
                                                <template v-for="(item, index) in roleList">
                                                    <Option :value="item.id" :key="index">{{item.name}}</Option>
                                                </template>
                                            </Select>
                                        </template>
                                    </div>
                                    <div class="tips">非超级管理员，禁止创建与当前角色同级的用户</div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    手机号码：
                                    <div><Input v-model="itemInfo.mobile" placeholder="手机号码" style="width: 100%" /></div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    邮箱：
                                    <div><Input v-model="itemInfo.email" placeholder="常用邮箱" style="width: 100%" /></div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    头像：
                                    <div>
                                        <template>
                                            <Upload action="//jsonplaceholder.typicode.com/posts/">
                                                <Button icon="ios-cloud-upload-outline">上传单张图片</Button>
                                            </Upload>
                                        </template>
                                    </div>
                                </td>
                            </tr>
                            <!-- <tr>
                                <td>
                                    <span class="request">*</span>帐号状态：
                                    <div>
                                        <RadioGroup>
                                            <Radio label="0">禁用</Radio>
                                            <Radio label="1">启用</Radio>
                                        </RadioGroup>
                                    </div>
                                </td>
                            </tr> -->
                        </table>
                    </div>
                </div>
                <div slot="footer" class="modalFooter">
                    <template>
                        <Button type="default" @click="isDetail=false" style="margin-right: 5px">关闭</Button>
                        <Button type="primary" @click="saveAction" :disabled="disable">提交</Button>
                    </template>
                </div>
            </Modal>
        </template>

    </div>
</template>

<script>
import md5 from "js-md5";
export default {
    name: "user",
    components: { },
    data: () => ({
        userInfo: null,
        userFuncInfo: null,
        height: window.innerHeight,
        isDetail: false,
        disable: false,
        isLoading: false,
        userFuncCode: {},
        count: 0,
        pageInfo: {
            pageIndex: 1,
            pageSize: 50,
        },
        searchInfo: {
            "userCode": "",
            "userName": "",
        },
        itemInfo: {
            "id": "",
            "email": "",
            "mobile": "",
            "nickname": "",
            "password": "",
            "username": "",
            "orgid": 0,
        },
        columnsRealTime: [
            {
                align: "center",
                title: "序号",
                type: "index",
                width: 60,
            },
            {
                title: "账号名",
                align: "center",
                key: "username",
            },
            {
                title: "姓名",
                align: "center",
                key: "nickname",
            },
            {
                title: "手机号",
                align: "center",
                key: "mobile"
            },
            {
                title: "邮箱",
                align: "center",
                key: "email",
            },
            {
                title: "角色",
                align: "center",
                key: "rolename",
            },
            {
                title: "状态",
                align: "center",
                key: "status",
                slot: "status"
            },
            {
                title: "创建时间",
                align: "center",
                key: "create_time",
            },
            {
                align: "center",
                title: "操作",
                width: 120,
                slot: 'action',
            }
        ],
        realTimeDataList: [],
        roleList: []
    }),
    methods: {
        pageSizeChange(value) {
            var self = this;
            self.pageInfo.pageIndex = parseInt(value, 10);
            self.getList(true);
        },
        pageRowChange(value) {
            var self = this;
            self.pageInfo.pageSize = parseInt(value, 10);
            self.getList(false);
        },
        reflesh(){
            var self = this;
            self.getList(true);
        },

        // 获取用户列表
        getList(bool){
            var self = this;
            if (bool == true) {
                self.isLoading = true;
                self.pageInfo.pageIndex = 0;
            }
            self.axios({
                method: 'get',
                url: self.$config.action.userList,
                params: {
                    "pageNum": self.pageInfo.pageIndex,
                    "pageSize": self.pageInfo.pageSize
                }
            })
            .then(function (res) {
                self.realTimeDataList = res.data.list;
                self.count = res.data.total;
                if(res.data.code=='0') {
                    
                } else if(res.data.code=='9003') {
                    self.utility.loginTimeOut(self);
                } else {
                    self.$Message.error(res.data.message);
                }
                self.isLoading = false;
            })
            .catch(function (error) {
                console.log(error);
                self.isLoading = false;
            });
        },
        // 显示新增
        showNew(){
            let self = this;
            self.itemInfo = {
                "id": "",
                "email": "",
                "mobile": "",
                "nickname": "",
                "password": "",
                "username": "",
                "orgid": 0,
            };
            self.isDetail = true;
        },
        // 显示编辑
        showEdit(item){
            let self = this;
            for(var key in item) {
                self.itemInfo[key] = item[key];
            }
            self.isDetail = true;
        },
        // 显示详情
        showInfo(item){
            let self = this;
            for(var key in item) {
                self.itemInfo[key] = item[key];
            }
            self.isInfo = true;
        },

        // 保存公司
        saveAction(){
            let self = this;
            
            if(self.itemInfo.username.trim().length<2||self.itemInfo.username.trim().length>50) {
                self.$Message.error("用户账号至少2到50位字符");
                return;
            }
            
            // 新增是秘密必传
            if(!self.itemInfo.id){
                if(self.itemInfo.password.trim().length<6||self.itemInfo.password.trim().length>18) {
                    self.$Message.error("登录密码至少6到18位字符");
                    return;
                }
            }
            
            self.disable = true;
            self.axios({
                method: 'post',
                url: !self.itemInfo.id?self.$config.action.userAdd:self.$config.action.userEdit,
                data: {
                    "id": self.itemInfo.id||"", 
                    "email": self.itemInfo.email,
                    "mobile": self.itemInfo.mobile,
                    "nickname": self.itemInfo.nickname,
                    "password": md5(self.itemInfo.password),
                    "username": self.itemInfo.username,
                    "orgid": self.itemInfo.orgid,
                }
            })
            .then(function (res) {
                if(res.data.code=='0') {
                    self.$Message.success("提交成功");
                    self.getList(false);
                    self.isDetail = false;
                    self.itemInfo = {
                        "id": "",
                        "email": "",
                        "mobile": "",
                        "nickname": "",
                        "password": "",
                        "username": "",
                        "orgid": ""
                    };
                } else if(res.data.code=='9003') {
                    self.utility.loginTimeOut(self);
                } else {
                    self.$Message.error(res.data.msg);
                }
                self.disable = false;
            })
            .catch(function (error) {
                console.log(error);
                self.disable = false;
            });
        },
        // 删除用户
        deleteAction(item){
            let self = this;
            self.$Modal.confirm({
                "title": "确定删除"+ item.username+"?",
                onOk() {
                    self.axios({
                        method: 'post',
                        url: self.$config.action.userDelete,
                        data: {
                            "id": item.id,
                        }
                    })
                    .then(function (res) {
                        if(res.data.code=='0') {
                            self.$Message.success("删除成功");
                            self.getList(true);
                        } else if(res.data.code=='9003') {
                            self.utility.loginTimeOut(self);
                        } else {
                            self.$Message.error(res.data.message);
                        }
                    })
                    .catch(function (error) {
                        console.log(error);
                    });
                }
            });
        },
        // 启用或禁用
        enableOrNot(value, row){
            let self = this;
            self.axios({
                method: 'post',
                headers: {
                    token: self.userInfo.token
                },
                url: value==1?self.$config.action.userEnable:self.$config.action.userDisable,
                data: {
                    "id": row.id
                }
            })
            .then(function (res) {
                if (res.data.code == '0') {
                    self.$Message.success("修改成功");
                    self.getList(false);
                } else if (res.data.code == "419") {
                    self.$utility.loginTimeOut(self);
                } else {
                    self.$Message.error(res.data.msg);
                }
            })
            .catch(function (error) {
                console.log(error);
            });
        },

        getRoleList(){
            var self = this;
            self.axios({
                method: 'get',
                url: self.$config.action.roleList,
                params: {
                    "pageNum": 1,
                    "pageSize": 1000,
                }
            })
            .then(function (res) {
                self.roleList = res.data.list;
                if(res.data.code=='0') {
                    self.roleList = res.data.list;
                } else if(res.data.code=='9003') {
                    self.utility.loginTimeOut(self);
                } else {
                    self.$Message.error(res.data.msg);
                }
            })
            .catch(function (error) {
                console.log(error);
            });
        }
    },
    created() {
        let self = this;
        let userTimeOut = null;
        self.userInfo = self.$utility.getLocalStorage("userInfo");
        self.userFuncInfo = self.$utility.getSessionStorage("userFuncInfo");
        self.userFuncCode = self.$utility.getSessionStorage("userFuncCode");
        
        self.getList(true);

        self.getRoleList();

        self.$watch('searchInfo', function () {
            clearTimeout(userTimeOut);
            userTimeOut = setTimeout(() => {
                self.getList(true);
            }, 200);
        }, {
            deep: true
        });
    },
    mounted() {
        let self = this;
        let resizeTime = null;
        window.onresize = function() {
            clearTimeout(resizeTime);
            resizeTime = setTimeout(function () {
                self.height = window.innerHeight;
            }, 500);
        };
    },
    beforeDestroy(){
        clearInterval(window.recordTime);
    }
};
</script>

<style lang="less">
@import "~@/common/unit.less";
</style>
